import Vue from 'vue'
import Router from 'vue-router'
// 一级
import Home from './components/homePage.vue'
import Menu from './components/menu.vue'
import Admin from './components/admin.vue'
import About from './components/about.vue'
import Login from './components/login.vue'
import Register from './components/register.vue'
// 二级
import Contact from './components/about/contactWe.vue'
import Express from './components/about/express.vue'
import History from './components/about/history.vue'
import Order from './components/about/orderDocument.vue'
// 三级
import OrderId from './components/about/history/order-id.vue'
import Price from './components/about/history/price.vue'
import Phone from './components/about/contact/phone.vue'
import PhoneName from './components/about/contact/phoneName.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    // 一级路由
    // "/"在哪 当前首页展示的组件就是哪个 一个就写component多个就写对象形式components
    {path: '/',name: 'home',components: {
      default: Home,
      history: History,
      contact: Contact,
      order: Order,
      express: Express
    }},
    {path: '/menu',name: 'menu',component: Menu},
    {path: '/admin',name: 'admin',component: Admin,
    // 路由独享守卫
      // beforeEnter: (to,from,next) =>{
      //   // if(这里判断当前是否登录){}vuex知识  判断 store.gettes.isLogin ===false
      //   alert('请先登录! 非登录状态不能访问');
      //   next('/login')
      // }
    },
    {path: '/about',name: 'about',redirect: '/about/history',component: About,children: [
// 二级路由
        {path: '/about/history',name: 'history',redirect: '/about/history/price',component: History,children: [
          // 三级路由
          {path: '/about/history/order-id' ,name: 'order-id',component: OrderId},
          {path: '/about/history/price' ,name: 'price',component: Price}
        ]},
        {path: '/about/contact',name: 'contact',redirect: '/about/contact/phoneName',component: Contact,children: [
          // 三级路由
          {path: '/about/contact/phone' ,name: 'phone',component: Phone},
          {path: '/about/contact/phoneName' ,name: 'phoneName',component: PhoneName}
        ]},
        {path: '/about/order',name: 'order',component: Order},
        {path: '/about/express',name: 'express',component: Express},
    ]},
    {path: '/login',name: 'login',component: Login},
    {path: '/register',name: 'register',component: Register},


    // 如果都不是就跳转首页
    {
      path: '*',
      redirect: '/'
    }
  ],
  
  // 控制滚动行为
  scrollBehavior: (to,from,savePosition) =>{
    // return { x:0, y:100} //当再次来到主页面时,页面展示在这个位置下
    return {selector: '#btn'}//当再次来到主页面时,btn按钮在最顶上
    // if(savePosition){ //只有回退或者前进可以进来 记录的是当前浏览位置 下次进来还是展示在此位置上
    //   console.log('进来没')
    //   return savePosition
    // }else{
    //   return { x:0, y:0}
    // }
  }
})
// 嵌套路由只需在需要嵌套的父路径对象加一条属性: children:[] 数组内继续写需要嵌套的路径对象
// 
// 需要某一组件默认打开即显示的话 在其父路径加一条属性redirecr:'需要显示的路径地址' (一级路径在想要默认显示某一组件只需设置此
// 路径地址为'/'即可)
// 


